body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url("https://www.transparenttextures.com/patterns/black-thread-light.png");
  
}
@font-face {
  font-family: gan;
  src: url(../../static/media/ganclm_bold-webfont.21316590.woff) format('woff'), 
   url(../../static/media/ganclm_bold-webfont.95c68112.ttf) format('truetype'); /* Chrome 4+, Firefox 3.5, Opera 10+, Safari 3—5 */
}  

.bodyDiv {
  width : 90%;
  margin: 10px auto;
}
.navDiv {
  margin: auto;
  width : 90%;
}

.big-logo-img {
  width:90%;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.prod-img{
  width: 100%;
  height: auto;
  max-width: 300px;
  box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%), 0 6px 20px 0 rgb(0 0 0 / 19%);
}

.prodTitle{
  font-family: gan;
  font-size: 20px;
}

.App {
  text-align: center;
}

h2, h1{
  border: 2px;
  font-family: gan;
  font-size: 40px;
  background: #CF9FC7;
  background: linear-gradient(to right, #CF9FC7 0%, #CF82BB 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black;
  text-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%), 0 6px 20px 0 rgb(0 0 0 / 19%);


}

.contact-icon{
  width: auto;
  height: 40px;
  margin: 0 10px;
}

.insta-img {
  width: 100%;
  height: auto;
  margin: 5px auto;
  padding: 2px;
  box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%), 0 6px 20px 0 rgb(0 0 0 / 19%);
}

.insta-img:hover {
  opacity: 0.7;
  background-color: #00000061;
}

.nav-link:hover {
  opacity: 0.7;
  color: white;
}

.logo-img{
  width: 50px;
  height: 50px;
  padding: 0 5px 0 0;
}

.insta-part{
   margin: auto;
}

.row {
  margin: 10px;
}

@media only screen and (max-width: 850px) {

  .navDiv {
    width : 100%;
  }
  .bodyDiv {
    width : 100%;
  }
  .insta-part{
    width: 90%;
  }
  .big-logo-img {
    width: 100%;
  }
}

._2R-kh {
    display: flex;
    flex-wrap: wrap;
  }
  ._wPDyp {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc(100%/6 - 10px);
    margin: 5px;
    display: block;
    position: relative;
  }
  ._vzTHL {
    display: block;
    width: 100%;
    height: 100%;
    transition: .25s;
    object-fit: cover;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
  }
  ._3xnQP {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    font-weight: 600;
    font-size: 12px;
    color: #fff;
    opacity: 0;
  }
  ._3xnQP:hover {
    opacity: 1;
    background-color: #00000061;
  }
  @media (max-width: 480px) {
    
    /* CSS */
    ._wPDyp{
      flex-grow: 0;
      flex-shrink: 0;
      flex-basis: calc(100%/2 - 10px);
      margin: 5px;
    }
    
  }
  
  @media (min-width: 481px) and (max-width: 767px) {
    
    /* CSS */
    ._wPDyp {
      flex-grow: 0;
      flex-shrink: 0;
      flex-basis: calc(100%/3 - 10px);
      margin: 5px;
    }
    
  }

  ._3lhLL{
    text-align: center;
    color: red;
    font-size: 18px;
  }

 


.invite-page {
    margin: 30px auto;
}

.invite-part{
    width: 40%;
    margin: 30px auto;
    padding: 30px 0;
    border: 1px solid #ccc!important;
    border-radius: 16px
}

.choose-area{
    border: 1px solid #ccc!important;
    border-radius: 16px
}

.btn-outline-info,.btn-info{
    padding: 2px;
    white-space: normal;
}

.form-check-input{
    margin: auto;
    padding: 0 10px;
}

.form-check-label{
    margin-top: 20px;
}

.form-group{
    margin: 5px 0;
    
}



.form-control{
    text-align: right;
    width: 40%;
    margin-right: 0;
    margin-left: auto;
    
}

.form-check{
    
    margin-bottom: auto;
}
.col-sm-4{
    padding: 5px;
}

.my-1{
    text-align: right;
    width: 40%;
    margin-right: 0;
    margin-left: auto;
    padding: 0;
}
.custom-select{
    text-align: right;
}

.eval-price-text{
    direction: rtl;
}

.col-form-label{
    
    direction: rtl;
    padding: 0;
    margin: auto;
}



@media only screen and (max-width: 1000px) {

    .invite-part{
        width: 60%;
        border-radius: 16px
    }
    .form-control {
        width: 70%;
    }
  }

  @media only screen and (max-width: 800px) {

    .invite-part{
        width: 80%;
        border-radius: 16px
    }
  }
